<template>
  <div class="create-account-thanks">
    <h2>Bedankt voor uw bestelling!</h2>
    <p>Uw bestelling komt uw kant op. Hieronder vind u een overzicht van uw bestelling.</p>
    <BRow class="content">
      <BCol md="6" class="d-flex">
        <div class="selected-image-holder">
          <BLink href="#" @click.stop="UpdateIsInWishList">
            <i class="lnr lnr-heart"></i>
            <div class="heart-icon" v-if="isInWishList">
              <img src="@/base/assets/heart.png" alt="heart" />
            </div>
          </BLink>
          <div class="selected-image">
            <img src="@/base/assets/productPageGalleryImg01.jpg" alt="heart" />
          </div>
        </div>
        <div class="text">
          <h4>Rogelli fietsjack winter Trani 3.0 Zwart/Rood</h4>
          <dl class="d-list">
            <dt>Prijs per stuk:</dt>
            <dd>€97,99</dd>
            <dt>Aantal:</dt>
            <dd>1</dd>
            <dt>Kleur:</dt>
            <dd>Rood</dd>
            <dt>Maat:</dt>
            <dd>L</dd>
          </dl>
        </div>
      </BCol>
      <BCol md="6">
        <h4>Bestelgegevens</h4>
        <dl class="d-list list-custom">
          <dt>Bestelstatus:</dt>
          <dd>Klaar voor verzending</dd>
          <dt>Bestelnummer:</dt>
          <dd>1121438509</dd>
          <dt>Besteldatum:</dt>
          <dd>23-08-20 10:12</dd>
        </dl>
        <BRow cols="2">
          <BCol sm="4">
            <span class="label-address">Factuuradres:</span>
            <address>
              <span>Jan Janssen</span>
              <span>Grotestraat 10 </span>
              <span>1234 AB, AMSTERDAM</span>
              <span>NL</span>
            </address>
          </BCol>
          <BCol sm="4">
            <span class="label-address">Leveradres:</span>
            <address>
              <span>Jan Janssen</span>
              <span>Grotestraat 10 </span>
              <span>1234 AB, AMSTERDAM</span>
              <span>NL</span>
            </address>
          </BCol>
        </BRow>
      </BCol>
    </BRow>
  </div>
</template>

<script setup>
import { computed, ref } from "vue";
import { useStore } from "vuex";
import { useRoute } from "vue-router";
import wishListAdapter from "@/base/helpers/adapters/wishListAdapter";

// Vuex store and route setup
const store = useStore();
const route = useRoute();

// Reactive state
const isInWishList = ref(true);

// Vuex getters using computed properties
const product = computed(() => {
  const selectedProduct = store.getters.getProduct(route.params.id);
  return wishListAdapter.productItem(selectedProduct || store.getters.getAllProducts[0]);
});

// Vuex actions
const { setProductStatus } = store.dispatch;

// Methods
function UpdateIsInWishList() {
  setProductStatus({ productId: product.value.id });
  isInWishList.value = !isInWishList.value;
}
</script>
